import { getCode, toLoginIn } from '@/api/login'
import Debounce from '@/util/debounce'
import comHeader from '@/components/header/header'

let t = null // 发送验证码的60秒定时器
export default {
  name: 'login',
  components: {
    comHeader
  },
  data () {
    return {
      phone: '',
      captcha: '',
      isCaptcha: false, // 是否发送验证码
      time: 59,
      isAllow: true
    }
  },
  methods: {
    handleCaptcha () {
      const rules = {
        tel: {
          name: "手机号",
          type: "validMobile",
          value: this.phone,
          required: !0
        }
      }
      if (!this.$utils.dataValidityCheck(rules)) return
      this.isCaptcha = true
      this._onTime() // 重新获取倒计时开始
      Debounce(() => this._onCaptcha(), 1000)
    },
    async _onCaptcha () {
      let params = {
        phone: this.phone,
        smsType: '8805'
      }
      getCode(params).then(({ data: { code, data, msg } }) => {
        if (code === 1) {
          this.$vux.toast.text('验证码发送成功')
        }
      }).catch((error) => {
        console.log('error', error)
      })
    },
    handleLogin () {
      const rules = {
        tel: {
          name: "手机号",
          type: "validMobile",
          value: this.phone,
          required: !0
        },
        code: {
          name: "验证码",
          type: "validPostalCode",
          value: this.captcha,
          required: !0
        }
      }
      if (!this.$utils.dataValidityCheck(rules)) return
      if (!this.isAllow) {
        return this.$vux.toast.text('请先同意用户协议')
      }
      Debounce(() => this._onLogin(), 1000)
    },
    async _onLogin () {
      let params = {
        loginName: this.phone,
        code: this.captcha,
        source: '3'
      }
      toLoginIn(params).then(({ data: { code, data, msg } }) => {
        if (code === 1) {
          this.$vux.toast.text('登录成功')
          this.$router.push('/certification')
        }
      }).catch(() => {
        this.$vux.toast.text('网络出错，请稍后重试')
      })
    },
    _onTime () {
      let t = setInterval(() => {
        this.time--
        if (this.time <= 0) {
          clearInterval(t)
          this.isCaptcha = false
          this.time = 59
        }
      }, 1000)
    }
  }
}



